import { CheckboxField, CheckboxGroupField } from '@wk/components-react16';
import React from 'react';

const SecurityQuestions = ({ securityquestions }) => {
  return (
    <>
      {securityquestions.map((data, id) => {
        const { question, answer } = data;
        const isChecked = (answer.toLowerCase() === 'true');
        return (
          <div key={id}>
            <CheckboxGroupField>
              <CheckboxField label={question}>
                <input
                  name="checkbox1"
                  type="checkbox"
                  checked={isChecked}
                  onChange={(event) => console.log(event)}
                />
              </CheckboxField>
            </CheckboxGroupField>
          </div>
        );
      })}
    </>
  );
};

export { SecurityQuestions };
