import { CheckboxField, CheckboxGroupField } from '@wk/components-react16';
import React from 'react';

const Aggreements = ({ aggreements }) => {
  return (
    <>
      {aggreements.map((data, id) => {
        const { aggreement, value } = data;
        const isChecked = (value.toLowerCase() === 'true');
        return (
          <div key={id}>
            <CheckboxGroupField>
              <CheckboxField label={aggreement}>
                <input
                  name={'checkbox' + id}
                  type="checkbox"
                  checked={isChecked}
                  onChange={(event) => console.log(event)}
                />
              </CheckboxField>
            </CheckboxGroupField>
          </div>
        );
      })}
    </>
  );
};

export { Aggreements };
