import React from 'react';

import { Banner, BannerLogo } from '@wk/components-react16';
import { useNavigate } from 'react-router-dom';

const Header = () => {
  const navigate = useNavigate();
  const handleLogoClick = (e) => {
    navigate('/');
  };
  return (
    <div className="navbar-story4">
      <Banner productName="Wolters Kluwer">
        <BannerLogo
          caption="Go to Team Contor home page"
          alt="Go to Team Contor home page"
          isPreventDefault
          slot="bannerLogo"
          onLogoClick={handleLogoClick}
        ></BannerLogo>
      </Banner>
    </div>
  );
};

export { Header };
