import React from 'react';

const ProfessionalInfo = ({ profile }) => {
  const {
    profession,
    Speciality,
    Workspace,
    AdditionalSpecialties,
    professionSchool,
    YearOfGraduation,
    Department,
    Designation,
    Institute,
    organization
  } = profile;
  // console.log(profile);
  return (
    <>
      <div className="user-personal-info">
        <div>
          <h3>Professional Information</h3>
          {Department && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Department</label>
              <label className="col-sm-10 col-form-label">{Department}</label>
            </div>
          )}
          {Designation && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Designation</label>
              <label className="col-sm-10 col-form-label">{Designation}</label>
            </div>
          )}
          {profession && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Profession</label>
              <label className="col-sm-10 col-form-label">{profession}</label>
            </div>
          )}
          {professionSchool && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Profession School</label>
              <label className="col-sm-10 col-form-label">{professionSchool}</label>
            </div>
          )}
          {YearOfGraduation && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Year Of Graduation</label>
              <label className="col-sm-10 col-form-label">{YearOfGraduation}</label>
            </div>
          )}
          {Speciality && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Speciality</label>
              <label className="col-sm-10 col-form-label">{Speciality}</label>
            </div>
          )}
          {AdditionalSpecialties && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">
                Additional Speciality
              </label>
              <label className="col-sm-10 col-form-label">
                {AdditionalSpecialties && AdditionalSpecialties.join(', ')}
              </label>
            </div>
          )}
          {Workspace && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Workspace</label>
              <label className="col-sm-10 col-form-label">{Workspace}</label>
            </div>
          )}
          {organization && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Organization</label>
              <label className="col-sm-10 col-form-label">{organization}</label>
            </div>
          )}
          {Institute && (
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Institute</label>
              <label className="col-sm-10 col-form-label">{Institute}</label>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export { ProfessionalInfo };
