import React, { useState } from 'react';
import { AvatarUserPlaceholder } from '@wk/components-react16';
import { PersonalInfoMaster } from './personalInfoMaster';

const MasterIdentity = ({ profileData, showAllProfile, hideAllProfile }) => {
  const {
    firstName,
    lastName,
    jobTitle,
    salutation
  } = profileData;
  const [showHideProfile, setShowHideProfile] = useState(false);

  const updateDisplayText = () => {
    setShowHideProfile(!showHideProfile);
  };

  return (
    profileData && (
      <>
        <p>
          This is your personal profile information that is being used accross
          Wolters Kluwer products.
        </p>
        <div className="user-profile-details">
          <div className="back-to-profile">
            {!showHideProfile ? (
              <span
                onClick={() => {
                  updateDisplayText();
                  showAllProfile();
                }}
              >
                Show All Profile
              </span>
            ) : (
              <span
                onClick={() => {
                  updateDisplayText();
                  hideAllProfile();
                }}
              >
                Hide All Profile
              </span>
            )}
          </div>
          <AvatarUserPlaceholder size="extra-large"></AvatarUserPlaceholder>
          <div className="user-data">
            <div className="user-name">
              {salutation} {firstName} {lastName}
            </div>
            <div className="user-designation">{jobTitle}</div>
          </div>
          <div className="user-personal-info mt-4">
            <h2>Master Identity</h2>
            <PersonalInfoMaster profile={profileData} />
          </div>
        </div>
      </>
    )
  );
};

export { MasterIdentity };
