import React, { useEffect, useState } from 'react';
import { AddressDetails } from './addressDetails';
import { ProfessionalInfo } from './professionalInfo';
import { PersonalInfo } from './personalInfo';
import { ButtonField, Smokebreak } from '@wk/components-react16';
import { showNotification } from '../NotificatonPopup/showNotification';
import axios from 'axios';
import ReactJson from '@microlink/react-json-view';
import { SecurityQuestions } from './securityQuestions';
import { Aggreements } from './aggreements';
const baseURL =
  'https://wk-cg2023-contour-func-apps.azurewebsites.net/api/GetUserProfilebyProduct?code=9sDbgvUnSR7y0jqbSOuqUyS9PQ7wplshTWaGWMz_XmaqAzFuOY7DpQ==';
const updateBaseURL =
  'https://wk-cg2023-contour-func-apps.azurewebsites.net/api/add-profile?code=0DjIDyEuZ-t3r_e6OW-v9IAUn5Uaub1ZSykVSnUlDeklAzFuNjjDVA==';

const ProjectInfo = (props) => {
  const { productName, showProfile, userid, updateIsUserData } =
    props;
  const [isOpen, setIsOpen] = useState(false);
  const [updatedUserProfile, setUpdatedUserProfile] = useState({});
  const [userProfile, setUserProfile] = useState({});
  const { address, securityquestions, aggreements } = userProfile;

  useEffect(() => {
    GetUserProfilebyProduct();
    setUpdatedUserProfile({});
    // console.log(productName);
  }, [productName]);

  const GetUserProfilebyProduct = async () => {
    const payload = {
      Id: userid,
      appName: productName
    };
    axios
      .post(baseURL, payload)
      .then(function (response) {
        setUserProfile(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const openEditor = (event) => {
    // console.log(userProfile);
    setIsOpen(true);
  };

  const closeEditor = (event) => {
    setIsOpen(false);
  };

  const saveData = (event) => {
    console.log(updatedUserProfile);
    const url = `${updateBaseURL}&appName=${productName}`;
    axios
      .post(url, updatedUserProfile)
      .then(function (response) {
        if (response.status === 200) {
          showNotification('success', 'Profile updated successfully');
          setIsOpen(false);
          GetUserProfilebyProduct();
          updateIsUserData();
        }
        // console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const updatedMasterData = (data) => {
    const { updated_src } = data;
    setUpdatedUserProfile(updated_src);
  };

  return (
    userProfile && (
      <>
        <div className="back-to-profile">
          <span onClick={showProfile}>Back to profile page</span>
        </div>
        <div className="user-profile-details">
          <div className="user-personal-info">
            <h2>Product Information</h2>
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Product Name</label>
              <label className="col-sm-10 col-form-label">{productName}</label>
            </div>
          </div>
        </div>
        <div className="m-2 text-right">
          <ButtonField>
            <button onClick={openEditor}>Edit JSON Data</button>
          </ButtonField>
        </div>
        <div>
          <Smokebreak
            isOpen={isOpen}
            position="right"
            size="content"
            hasStickyHeader={true}
            onUserRequest={closeEditor}
          >
            <div
              id="smokebreak-id-001"
              className="smokebreak-pds-suggestion-header"
              slot="smokebreakHeader"
              style={{
                margin: '1rem 1.5rem'
              }}
            >
              <div className="user-profile-edit">
                <div className="edit-column">
                  <h1>Edit User Profile</h1>
                </div>
                <div className="edit-column edit-button-container">
                  <div className="button-position">
                    <ButtonField>
                      <button onClick={saveData}>Save Data</button>
                    </ButtonField>
                    &nbsp;
                    <ButtonField>
                      <button onClick={closeEditor}>Cancel</button>
                    </ButtonField>
                  </div>
                </div>
              </div>
            </div>
            <div>Test</div>
            <div
              style={{
                wordBreak: 'break-word',
                margin: '0 1.5rem 1rem'
              }}
              slot="smokebreakBody"
            >
              <ReactJson
                name="userProfile"
                src={userProfile}
                theme="ocean"
                onEdit={(data) => {
                  updatedMasterData(data);
                }}
                onAdd={(data) => {
                  updatedMasterData(data);
                }}
              />
            </div>
          </Smokebreak>
        </div>

        <div>
          <div className="user-profile-details">
            <div className="user-personal-info">
              <ProfessionalInfo profile={userProfile} />
            </div>
          </div>

          <div className="user-profile-details">
            <div className="user-personal-info">
              <h3>Personal Information</h3>
              <PersonalInfo profile={userProfile} />
            </div>
          </div>

          {address && (
            <div className="user-profile-details">
              <div className="user-personal-info">
                <h3>Contact Information</h3>
                <div className="row">
                  <AddressDetails address={address} />
                </div>
              </div>
            </div>
          )}

          {securityquestions && securityquestions?.length > 0 && (
            <div className="user-profile-details">
              <div className="user-personal-info">
                <h3>Security Questions</h3>
                <SecurityQuestions securityquestions={securityquestions} />
              </div>
            </div>
          )}

          {aggreements && aggreements?.length > 0 && (
            <div className="user-profile-details">
              <div className="user-personal-info">
                <h3>Aggreements</h3>
                <Aggreements aggreements={aggreements} />
              </div>
            </div>
          )}
        </div>
      </>
    )
  );
};

export { ProjectInfo };
