import React from 'react';
import { ProfessionalInfo } from './professionalInfo';
import { PhoneInfo } from './phoneInfo';
import {
  CardContainer,
  CardContainerBody,
  PillSuggestion
} from '@wk/components-react16';
import { PersonalInfo } from './personalInfo';
import { AddressDetails } from './addressDetails';
import { SecurityQuestions } from './securityQuestions';
import { Aggreements } from './aggreements';

const ViewAllProfile = (props) => {
  const { profiles, masterProfile } = props;
  // console.log(masterProfile)

  return (
    <>
      <div className="user-profile-details">
        <h2>Profile Information</h2>
        <div>
          {profiles.map((profile, id) => {
            const {
              address,
              phone,
              securityquestions,
              aggreements,
              products: { name }
            } = profile;
            // console.log(profile);
            return (
              <div className="row mt-4" key={id}>
                <div>
                  <span className={`profile-pill-${name}`} key={id}>
                    <PillSuggestion>
                      <span>{name}</span>
                    </PillSuggestion>
                  </span>
                </div>

                <div
                  style={{
                    boxSizing: 'border-box',
                    padding: '1rem'
                  }}
                >
                  <CardContainer>
                    <CardContainerBody>
                      <div
                        style={{
                          boxSizing: 'border-box',
                          padding: '1rem'
                        }}
                      >
                        <ProfessionalInfo profile={profile} />
                        <div className="mt-4">
                          <h3>Personal Information</h3>
                          <PersonalInfo profile={profile} />
                        </div>
                        <div className="row">
                          {address && address?.length > 0 && (
                            <div className="mt-4">
                              <h3>Contact Information</h3>
                              <div className="row">
                                <AddressDetails address={address} />
                              </div>
                            </div>
                          )}
                          {/* {phone && phone?.length > 0 && (
                          <div className="mt-4">
                            <h3>Phone Information</h3>
                            <PhoneInfo phone={phone} />
                          </div>
                        )} */}
                        </div>
                        {securityquestions && securityquestions?.length > 0 && (
                          <div className="mt-4">
                            <h3>Security Questions</h3>
                            <SecurityQuestions
                              securityquestions={securityquestions}
                            />
                          </div>
                        )}
                        {aggreements && aggreements?.length > 0 && (
                          <div className="mt-4">
                            <h3>Aggreements</h3>
                            <Aggreements
                              aggreements={aggreements}
                            />
                          </div>
                        )}
                      </div>
                    </CardContainerBody>
                  </CardContainer>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export { ViewAllProfile };
