import React, { useState } from 'react';
import { ViewAllProfile } from './viewAllProfile';
import { MasterIdentity } from './masterIdentity';

const ProfileInfo = (props) => {
  const { masterProfile } = props;
  const { masterProfile: masterprofile, profiles } = masterProfile;
  const [isShowAllProfile, setIsShowAllProfile] = useState(false);
  // console.log(masterProfile)
  
  const showAllProfile = () => {
    setIsShowAllProfile(true);
  };

  const hideAllProfile = () => {
    setIsShowAllProfile(false);
  };
  // console.log(masterprofile);
  if (!masterprofile) {
    return;
  }

  return (
    <>
      <MasterIdentity
        profileData={masterprofile}
        showAllProfile={showAllProfile}
        hideAllProfile={hideAllProfile}
      />
      {isShowAllProfile && (
        <ViewAllProfile masterProfile={masterProfile} profiles={profiles} />
      )}
    </>
  );
};

export { ProfileInfo };
