import React from 'react';

import {
  WkFooter,
  FooterContainer,
  FooterNavigation,
  FooterItem,
  FooterNavigationLink,
  FooterCopyright
} from '@wk/components-react16';

const Footer = () => {
  return (
    <WkFooter mode="workflow">
      <FooterContainer mode="workflow">
        <FooterNavigation mode="workflow">
          <FooterItem>
            <FooterNavigationLink href="#">Disclaimer </FooterNavigationLink>
          </FooterItem>
          <FooterItem>
            <FooterNavigationLink href="#">
              Privacy statement{' '}
            </FooterNavigationLink>
          </FooterItem>
          <FooterItem>
            <FooterNavigationLink href="#">
              Terms &amp; conditions{' '}
            </FooterNavigationLink>
          </FooterItem>
        </FooterNavigation>
        <FooterCopyright template="%copy% %year% Team Contour" mode="workflow"></FooterCopyright>
      </FooterContainer>
    </WkFooter>
  );
};

export { Footer };
