import React, { useEffect, useState } from 'react';
import { VerticalNav, Iconsvg, ButtonGroupItem, ButtonField, ButtonGroup } from '@wk/components-react16';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { ProfileInfo } from './profileInfo';
import { ProjectInfo } from './projectInfo';
import { ErrorView } from '../errorView';

const baseURL =
  'https://wk-cg2023-contour-func-apps.azurewebsites.net/api/GetFullProfile?code=ic_DVb-1yoYTRweqgSVAykTPlX727DqwzIPr8Epq20IeAzFuGNtqJg==';

const Profile = (props) => {
  const { userid } = useParams();
  const [showProjectInfo, setShowProjectInfo] = useState(false);
  const [masterProfile, setMasterProfile] = useState({});
  const [masterProducts, setMasterProducts] = useState([]);
  const [masterProduct, setMasterProduct] = useState([]);
  const [productName, setProductName] = useState('');
  const [productId, setProductId] = useState('');
  const [selectedNodeId, setSelectedNodeId] = useState(null);
  const [expandedNodeIds, setExpandedNodeIds] = useState([]);
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isDataUpdated, setIsDataUpdated] = useState(1);

  useEffect(() => {
    const getMasterData = async () => {
      const url = `${baseURL}&id=${userid}`;
      setIsLoading(true);

      axios
        .get(url)
        .then((response) => {
          const masterData = response.data;
          setMasterProfile(masterData);
          const products = masterData.profiles.map(function (data, id) {
            return {
              productId: data.id,
              title: data.products.name,
              id: id + 1,
              isRoot: true,
              icon: 'cube'
            };
          });

          setMasterProducts(products);
          setIsError(false);
          setIsLoading(false);
        })
        .catch((error) => {
          setIsError(true);
          setIsLoading(false);
          console.log(error);
        });
    };
    getMasterData();
  }, [userid, isDataUpdated]);

  const onNodeClick = (node) => {
    setShowProjectInfo(true);
    setProductName(node.title);
    setProductId(node.productId);
    setSelectedNodeId(node.id);
    setExpandedNodeIds([node.id]);
    const product = masterProfile.profiles.filter(function (data, id) {
      let productData;
      if (data.products.name === node.title) {
        productData = data;
      }
      return productData;
    });
    setMasterProduct(product);
  };
  const showProfile = () => {
    setShowProjectInfo(false);
    setSelectedNodeId(null);
    setExpandedNodeIds([]);
  };

  const updateIsUserData = () => {
    const randNo = Math.random();
    setIsDataUpdated(randNo);
  };

  return (
    <>
      {isLoading ? (
        <div className="loading-container">
          <h2>Loading please wait...</h2>
        </div>
      ) : !isError ? (
        masterProfile && (
          <>
            <div className="user-profile-container">
              <div className="profile-menu">
                <VerticalNav
                  isSticky
                  controlMode="controlled"
                  nodes={masterProducts}
                  selectedNodeId={selectedNodeId}
                  expandedNodeIds={expandedNodeIds}
                  onNodeClick={onNodeClick}
                ></VerticalNav>
              </div>
              <div className="profile-info">
                {showProjectInfo ? (
                  <ProjectInfo
                    productName={productName}
                    productId={productId}
                    masterProduct={masterProduct}
                    showProfile={showProfile}
                    userid={userid}
                    updateIsUserData={updateIsUserData}
                  />
                ) : (
                  <>
                  <ButtonGroup>
                    <ButtonGroupItem slot="buttonGroupItem">
                      <ButtonField mode={'ghost'} iconPosition={'left'}>
                        <button
                          type="button"
                          onClick={updateIsUserData}
                        >
                          <Iconsvg name="refresh"></Iconsvg>
                          Refresh{' '}
                        </button>
                      </ButtonField>
                    </ButtonGroupItem>
                    </ButtonGroup>
                    <ProfileInfo masterProfile={masterProfile} />
                  </>
                )}
              </div>
            </div>
          </>
        )
      ) : (
        <div className="error-view-container">
          <ErrorView />
        </div>
      )}
    </>
  );
};

export { Profile };
