import React from 'react';
import { AvatarUserPlaceholder } from '@wk/components-react16';
import { getProducts } from '../../helpers';
import { Pills } from '../pills';

export const UserDetails = (props) => {
  const { details, onUserClick } = props;
  return (
    <div>
      <div className="row">
        {details &&
          details.map((detail, id) => {
            const { masterprofile, profiles: profile, uniqueUserId: userId } = detail;
            // console.log(detail);
            const { email, firstName, lastName } = masterprofile ?? {};
            const products = getProducts(profile);
            return (
              firstName && (
                <div
                  className="col-md-3 row-eq-height-card mb-3"
                  data-user-id={userId}
                  key={id}
                >
                  <article
                    className="profile-user-detail"
                    data-user-id={userId}
                    onClick={onUserClick}
                  >
                    <div className="user-profile-pic" data-user-id={userId}>
                      <AvatarUserPlaceholder size="extra-large"></AvatarUserPlaceholder>
                    </div>
                    <h2 data-user-id={userId}>
                      {firstName}, {lastName}
                    </h2>
                    <div className="user-applications" data-user-id={userId}>
                      <Pills products={products} userId={userId} />
                    </div>
                    <div data-user-id={userId}>Email: {email}</div>
                  </article>
                </div>
              )
            );
          })}
      </div>
    </div>
  );
};
