import React from 'react';
import {
  CardContainer,
  CardContainerBody,
  PillSuggestion
} from '@wk/components-react16';

const AddressDetails = ({ address, name }) => {
  return (
    <>
      {address.map((data, id) => {
        // console.log(data, name, id);
        const {
          address1,
          address2,
          city,
          countryCode,
          postalCode,
          stateProvinceCode,
          type
        } = data;
        return (
          <div className="col-md-3 row-eq-height-card mb-3" key={id}>
            <CardContainer>
              <CardContainerBody>
                <div
                  style={{
                    boxSizing: 'border-box',
                    minHeight: '150px',
                    padding: '15px'
                  }}
                >
                  {type && (
                    <div className={`address-pill-${type}`}>
                      <PillSuggestion>
                        <span>{type} Address</span>
                      </PillSuggestion>
                    </div>
                  )}
                  <div className="mt-3">
                    {address1} {address2} {city} {stateProvinceCode}{' '}
                    {postalCode} {countryCode}
                  </div>
                </div>
              </CardContainerBody>
            </CardContainer>
          </div>
        );
      })}
    </>
  );
};

export { AddressDetails };
