import React, { useEffect, useState } from 'react';
import { SearchField } from '@wk/components-react16';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { UserDetails } from './userDetails';
import { ErrorView } from '../errorView';
import { NoDataFound } from '../noDataFound';

const baseURL =
  'https://wk-cg2023-contour-func-apps.azurewebsites.net/api/SearchService?code=YR5ale2ubCXC-mjbTKXGHrkcKmGrx8Z8Y7FsXULpOQxYAzFukvquQQ==';

export const Home = (props) => {
  const [details, setDetails] = useState([]);
  // const [totalRecords, setTotalRecords] = useState();
  const [searchString, setSearchString] = useState('*');
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();
  useEffect(() => {
    const getDashboardData = async (searchString) => {
      const url = `${baseURL}&st=${searchString}`;
      setIsLoading(true);
      axios
        .get(url)
        .then((response) => {
          setDetails(response.data.value);
          // setTotalRecords(response.data['@odata.count']);
          setIsError(false);
          setIsLoading(false);
        })
        .catch((error) => {
          setIsError(true);
          setIsLoading(false);
          console.log(error);
        });
    };
    getDashboardData(searchString);
  }, [searchString]);

  const onUserClick = (event) => {
    const { userId } = event.target.dataset;
    if (userId) {
      navigate(`/user/${userId}`);
    } else {
      return;
    }
  };

  const searchOnKeyDown = (e) => {
    if (e.key === 'Enter') {
      const searchValue = e.target.value;
      searchValue !== '' ? setSearchString(searchValue) : setSearchString('*');
    }
  };

  const searchUser = (id) => {
    const searchValue = document.getElementById(id).value;
    searchValue !== '' ? setSearchString(searchValue) : setSearchString('*');
  };

  return (
    <div className="profile-home">
      <div className="profile-search">
        <h2>Search Profile</h2>
        <SearchField onSearch={() => searchUser('search-id-1-3')}>
          <input
            type="search"
            id="search-id-1-3"
            name="search-1-3"
            placeholder="Search Everywhere"
            onKeyDown={searchOnKeyDown}
          />
        </SearchField>
      </div>
      {isLoading ? (
        <h2>Loading please wait...</h2>
      ) : !isError ? (
        details.length > 0 ? (
          <UserDetails details={details} onUserClick={onUserClick} />
        ) : (
          <NoDataFound />
        )
      ) : (
        <ErrorView />
      )}
    </div>
  );
};
