import React from 'react';
import { PillSuggestion } from '@wk/components-react16';

export const Pills = ({ products, userId }) => {
  return (
    <>
      {products.map((product, id) => {
        return (
          <span className={`profile-pill-${product}`} key={id}>
            <PillSuggestion data-user-id={userId}>
              <span data-user-id={userId}>{product}</span>
            </PillSuggestion>
          </span>
        );
      })}
    </>
  );
};
